import Vue from 'vue'
import Router from 'vue-router'
import store from '../store/store';

import Main from '../components/main/index.vue'

const Login = r => require.ensure([], () => r(require('../components/main/login.vue')), 'logIn');
const Register = r => require.ensure([], () => r(require('../components/main/register.vue')), 'logIn');

const Chat = r => require.ensure([], () => r(require('../components/chat/index.vue')), 'messenger');

const Profile = r => require.ensure([], () => r(require('../components/profile/index.vue')), 'profile');
const ProfileEdit = r => require.ensure([], () => r(require('../components/profile/edit.vue')), 'profile');
const ProfileHome = r => require.ensure([], () => r(require('../components/profile/home.vue')), 'profile');
const ProfileUser = r => require.ensure([], () => r(require('../components/profile/user.vue')), 'profile');
const MyTeam = r => require.ensure([], () => r(require('../components/profile/myteam.vue')), 'profile');

const Goods = r => require.ensure([], () => r(require('../components/goods/index.vue')), 'goods');
const MyGoods = r => require.ensure([], () => r(require('../components/goods/mygoods.vue')), 'goods');
const GoodsCreate = r => require.ensure([], () => r(require('../components/goods/create.vue')), 'goods');
const GoodsEdit = r => require.ensure([], () => r(require('../components/goods/edit.vue')), 'goods');
const GoodsInfo = r => require.ensure([], () => r(require('../components/goods/info.vue')), 'goods');
const GoodsPage = r => require.ensure([], () => r(require('../components/goods/good.vue')), 'goods');

const Deal = r => require.ensure([], () => r(require('../components/deals/index.vue')), 'deals');

const Wishes = r => require.ensure([], () => r(require('../components/wishes/index.vue')), 'wishes');
const MyWishes = r => require.ensure([], () => r(require('../components/wishes/mywishes.vue')), 'wishes');
const WishesCreate = r => require.ensure([], () => r(require('../components/wishes/create.vue')), 'wishes');

const Services = r => require.ensure([], () => r(require('../components/services/index.vue')), 'services');
const MyServices = r => require.ensure([], () => r(require('../components/services/myservices.vue')), 'services');
const ServicesCreate = r => require.ensure([], () => r(require('../components/services/create.vue')), 'services');

const Error404 = r => require.ensure([], () => r(require('../components/errors/error404.vue')), 'errors');

Vue.use(Router);

export default new Router({
    mode: 'history', //активировать для хостинга
    store,
    routes: [
        {
            path: '/login',
            name: 'login',
            component: Login,
            beforeEnter (to, from, next) {
                // добавить проверку на наличие куков/сессий авторизации
                next();
            }
        },
        {
            path: '/register',
            // path: '/register/:id(\[\\d|\\w\]\{30\})', //филальная версия
            name: 'register',
            component: Register
        },
        {
            path: '/',
            name: 'main',
            component: Main
        },
        {
            path: '/profile/:id(\\d+)',
            component: ProfileUser
        },
        {
            path: '/profile',
            component: Profile,
            children: [
                {
                    path: '',
                    component: ProfileHome
                },
                {
                    path: 'edit',
                    component: ProfileEdit
                },
                {
                    path: 'team',
                    component: MyTeam
                },
                {
                    path: '*',
                    component: Error404
                }
            ]
        },
        {
            path: '/chat',
            component: Chat
        },
        {
            path: '/goods/:id(\\d)',
            component: GoodsPage,
            children: [
                {
                    path: '',
                    component: GoodsInfo
                },
                {
                    path: 'edit',
                    component: GoodsEdit
                }
            ]
        },
        {
            path: '/goods',
            component: Goods,
            children: [
                {
                    path: '',
                    component: MyGoods
                },
                {
                    path: 'create',
                    component: GoodsCreate
                },
                {
                    path: '*',
                    component: Error404
                }
            ]
        },
        {
            path: '/deal/:id(\\d)',
            component: Deal
        },
        {
            path: '/wishes',
            component: Wishes,
            children: [
                {
                    path: '',
                    component: MyWishes
                },
                {
                    path: 'create',
                    component: WishesCreate
                },
                {
                    path: '*',
                    component: Error404
                }
            ]
        },
        {
            path: '/services',
            component: Services,
            children: [
                {
                    path: '',
                    component: MyServices
                },
                {
                    path: 'create',
                    component: ServicesCreate
                },
                {
                    path: '*',
                    component: Error404
                }
            ]
        },
        {
            path: '*',
            component: Error404
        }
    ]
})



