<template>
    <div class="wrapper">
        <slideout menu="#menu" panel="#content" :toggleSelectors="['.menu-toogle']" padding="300" ref="mainMenu">
            <nav class="sidebar" id="menu">
                <header class="logo">
                    <img src="../assets/images/logo.png">
                    ДКП &laquo;Мы&raquo;<br>
                    <span>Мой вклад = мои возможности</span>
                </header>
                <div class="hidden-md hidden-lg hidden-xl hidden-xxl">
                    <div class="menu-profile acenter">
                        <img src="../assets/images/avatar.png" alt="avatar" class="img-circle">
                        <div class="fullname">{{fullname}}</div>
                        <button class="btn btn-white btn-small" @click="logOut">Выйти</button>
                    </div>
                    <div class="menu-balance">
                        <div class="line pull-right">{{aBalance}} рублей</div>
                        <div class="line">Активный баланс:</div>
                        <div class="line pull-right">{{pBalance}} рублей</div>
                        <div class="line">Пассивный баланс:</div>
                        <div class="actions acenter">
                            <button class="btn btn-white btn-small" @click="request">Запросить</button>
                            <button class="btn btn-white btn-small" @click="transfer">Перевести</button>
                            <button class="btn btn-white btn-small" @click="replenish">Пополнить</button>
                        </div>
                    </div>
                </div>
                <menu class="side-menu" @click="closeMenu">
                    <router-link class="line" to="/profile" tag="div" exact>
                        <a class="item"><i></i>Мой профиль</a><router-link class="shortcut" to="/profile/edit"><i class="fa fa-pencil"></i></router-link>
                    </router-link>
                    <router-link class="line" to="/profile/team" tag="div">
                        <a class="item"><i></i>Моя команда</a><router-link class="shortcut" to="/profile/invite/add"><i class="fa fa-user-plus"></i></router-link>
                    </router-link>
                    <router-link class="line" to="/chat" tag="div">
                        <a class="item"><i></i>Мои сообщения</a><router-link class="shortcut" to="/chat"><i>2</i></router-link>
                    </router-link>
                    <router-link class="line" to="/goods" tag="div">
                        <a class="item"><i></i>Товары</a><router-link class="shortcut" to="/goods/create"><i class="fa fa-plus"></i></router-link>
                    </router-link>
                    <router-link class="line" to="/services" tag="div">
                        <a class="item"><i></i>Услуги</a><router-link class="shortcut" to="/services/create"><i class="fa fa-plus"></i></router-link>
                    </router-link>
                    <router-link class="line" to="/wishes" tag="div">
                        <a class="item"><i></i>Желания</a><router-link class="shortcut" to="/wishes/create"><i class="fa fa-plus"></i></router-link>
                    </router-link>
                </menu>
            </nav>
            <div id="content">
                <nav class="topbar">
                    <div class="icon pull-left menu-toogle">
                        <i class="fa fa-bars"></i>
                    </div>
                    <div class="separator pull-left menu-toogle"></div>

                    <div class="separator pull-right"></div>
                    <el-tooltip effect="dark" content="Скоро..." placement="left">
                        <div class="icon pull-right">
                            <i class="fa fa-cogs"></i>
                        </div>
                    </el-tooltip>
                    <div class="separator pull-right hidden-zero hidden-xs hidden-sm hidden-md"></div>
                    <div class="pull-right hidden-zero hidden-xs hidden-sm hidden-md">
                        <dropdown :position="['right', 'bottom', 'right', 'top']" animation="ani-slide-y" name="profile" :margin="[0, 5]" :pWidth="true">
                            <div class="profile" @click="ddToggle('profile')">
                                <img src="../assets/images/avatar.png" alt="avatar" class="img-circle">
                                <span>{{fullname}}</span>
                                <i class="fa" :class="[ddStates.profile ? 'fa-chevron-up' : 'fa-chevron-down']"></i>
                            </div>
                            <div class="dropdown" slot="dropdown">
                                <div class="profile-dropdown">
                                    <img src="../assets/images/avatar.png" class="img-circle">
                                    <div class="fullname">{{fullname}}</div>
                                    <time class="since">{{auth.since}}</time>
                                    <div class="space10"></div>
                                    <div class="row">
                                        <div class="col-xs-12">
                                            <router-link to="/profile"><button class="btn btn-full btn-default btn-small" @click="ddStates.profile = false">Профиль</button></router-link>
                                        </div>
                                        <div class="col-xs-12">
                                            <button class="btn btn-full btn-default btn-small" @click="logOut">Выйти</button>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </dropdown>
                    </div>
                    <div class="separator pull-right hidden-zero hidden-xs hidden-sm"></div>
                    <div class="buttons pull-right hidden-zero hidden-xs hidden-sm">
                        <div>
                            <router-link to="/faq">
                                <div class="faq">
                                    <i class="fa fa-question"></i>
                                </div>
                            </router-link>
                        </div>
                        <dropdown :position="['right', 'bottom', 'right', 'top']" name="balance" :margin="[0, 18]" :class="{active: ddStates.balance}">
                            <div class="balance" @click="ddToggle('balance')">
                                <i class="fa fa-ruble"></i>
                            </div>
                            <div class="dropdown" slot="dropdown">
                                <div class="drop-line">
                                    <span class="pull-right">{{aBalance}} рублей</span>
                                    <span>Активный баланс</span>
                                </div>
                                <div class="drop-line">
                                    <span class="pull-right">{{pBalance}} рублей</span>
                                    <span>Пассивный баланс</span>
                                </div>
                                <div class="space10"></div>
                                <div class="row">
                                    <div class="col-xs-8">
                                        <button class="btn btn-full btn-default btn-small" @click="request">Запросить</button>
                                    </div>
                                    <div class="col-xs-8">
                                        <button class="btn btn-full btn-default btn-small" @click="transfer">Перевести</button>
                                    </div>
                                    <div class="col-xs-8">
                                        <button class="btn btn-full btn-primary btn-small" @click="replenish">Пополнить</button>
                                    </div>
                                </div>
                                <button class="btn btn-full btn-default btn-small" @click="balanceLog">История действий</button>
                            </div>
                        </dropdown>
                        <dropdown :position="['right', 'bottom', 'right', 'top']" name="notifications" :margin="[0, 18]" :class="{active: ddStates.notifications}">
                            <div class="notifications" @click="ddToggle('notifications')">
                                <i class="fa fa-bell-o"></i>
                            </div>
                            <div class="dropdown" slot="dropdown" style="padding:0;">
                                <scroll :style="{'max-height': '300px'}">
                                    <div class="notification-list">
                                        <div class="line warning">
                                            <div class="line-title">
                                                <i class="fa fa-fw fa-warning"></i> Сообщение с уведомлением
                                                <span class="line-time pull-right">18:55</span>
                                            </div>
                                            <div class="line-body">
                                                Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.
                                            </div>
                                        </div>
                                        <div class="line danger">
                                            <div class="line-title">
                                                <i class="fa fa-fw fa-exclamation"></i> Сообщение с ошибкой
                                                <span class="line-time pull-right">18:41</span>
                                            </div>
                                            <div class="line-body">
                                                Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.
                                                <div class="actions aright">
                                                    <button class="btn btn-primary btn-small">Повторить</button>
                                                    <button class="btn btn-danger btn-small">Отменить</button>
                                                </div>
                                            </div>
                                        </div>
                                        <div class="line success">
                                            <div class="line-title">
                                                <i class="fa fa-fw fa-check"></i> Успешное сообщение
                                                <span class="line-time pull-right">18:30</span>
                                            </div>
                                        </div>
                                        <div class="line info">
                                            <div class="line-title">
                                                <i class="fa fa-fw fa-info"></i> Информационное сообщение
                                                <span class="line-time pull-right">18:23</span>
                                            </div>
                                            <div class="line-body">
                                                Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.
                                            </div>
                                        </div>
                                    </div>
                                </scroll>
                                <div class="all acenter">
                                    Показать все
                                </div>
                            </div>
                        </dropdown>
                    </div>
                    <div class="separator pull-right hidden-zero hidden-xs"></div>
                    <div class="pull-right hidden-zero hidden-xs">
                        <dropdown :position="['right', 'bottom', 'right', 'top']" animation="ani-slide-y" name="search" :margin="[0, 5]" :pWidth="true">
                            <div class="search-type" @click="ddToggle('search')">
                                <input type="hidden" name="search-type" value="catalog">
                                <span class="type">{{searchTypes[searchTypeCurrent]}}</span>
                                <i class="fa" :class="[ddStates.search ? 'fa-chevron-up' : 'fa-chevron-down']"></i>
                            </div>
                            <div class="dropdown select" slot="dropdown">
                                <div class="drop-line aright" @click="selectSearchType('all')">
                                    Поиск по сайту
                                </div>
                                <div class="drop-line aright" @click="selectSearchType('catalog')">
                                    Поиск по товарам
                                </div>
                                <div class="drop-line aright" @click="selectSearchType('service')">
                                    Поиск по услугам
                                </div>
                            </div>
                        </dropdown>
                    </div>
                    <div class="separator pull-right"></div>

                    <div class="search">
                        <input type="text" class="search-input hidden-zero hidden-xs hidden-sm" placeholder="Что Вы хотели бы найти?">
                        <input type="text" class="search-input hidden-md hidden-lg hidden-xl hidden-xxl" placeholder="Поиск...">
                    </div>
                </nav>
                <div class="color-sep"></div>
                <div class="content">
                    <transition :enter-active-class="'animated05 fadeIn'" :leave-active-class="'animated05 fadeOut'" :duration="500" mode="out-in">
                        <router-view></router-view>
                    </transition>
                </div>
                <footer>
                    <div class="row rules">
                        <div class="aright">
                            <router-link to="/user-agreement">Пользовательское соглашение</router-link>
                        </div>
                        <div class="sep"></div>
                        <div>
                            <router-link to="/faq">Часто задаваемые вопросы</router-link>
                        </div>
                    </div>
                    <div class="row copyright">
                        <div>Все права защещены. ДКП &laquo;Мы&raquo; &copy; 2017</div>
                    </div>
                </footer>
            </div>
        </slideout>

        <el-dialog title="Пополнение лицевого счета" v-model="replenishDialog" size="tiny">
            <span>Для пополнение вашего лицевого счета переведите нужную сумму на счет карты:</span>
            <blockquote class="main">1234 5678 9012 3456</blockquote>
            <span slot="footer" class="dialog-footer">
                <button class="btn btn-primary btn-small" @click="replenishDialog = false">Понятно</button>
            </span>
        </el-dialog>
        <el-dialog title="Перевод средств моей команде" v-model="transferDialog" size="tiny">
            <span class="input" :class="{'input-filled': transferMoney}">
                <el-select v-model="transferUser" placeholder="Выберите пользователя" :class="{'input-filled': transferUser}">
                    <el-option
                            v-for="item in myTeam"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                    </el-option>
                </el-select>
            </span>
            <span class="input" :class="{'input-filled': transferMoney}">
                <input class="input-field" type="number" min="1" :max="auth.aBalance" v-model="transferMoney"/>
                <label class="input-label">
                    <span class="input-label-content">Введите сумму</span>
                </label>
            </span>
            <span slot="footer" class="dialog-footer">
                <button class="btn btn-default btn-small" @click="transferDialog = false">Отменить</button>
                <button class="btn btn-primary btn-small" @click="transferDialog = false">Отправить</button>
            </span>
        </el-dialog>
        <el-dialog title="Запрос средств у моей команды" v-model="requestDialog" size="tiny">
            <span class="input" :class="{'input-filled': requestMoney}">
                <input class="input-field" type="number" min="1" v-model="requestMoney"/>
                <label class="input-label">
                    <span class="input-label-content">Введите сумму</span>
                </label>
            </span>
            <span slot="footer" class="dialog-footer">
                <button class="btn btn-default btn-small" @click="requestDialog = false">Отменить</button>
                <button class="btn btn-primary btn-small" @click="requestDialog = false">Отправить</button>
            </span>
        </el-dialog>
        <el-dialog title="История действий" v-model="historyDialog" size="tiny">
            <div class="balance-log">
                <div class="action-line">Текстирование <div class="pull-right date">12 декабря 2017, 12:30</div></div>
            </div>
            <span slot="footer" class="dialog-footer">
                <button class="btn btn-default btn-small" @click="historyDialog = false">Закрыть</button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
    import slideout from 'vue-slideout'
    import dropdown from './global/dropdown.vue'
    import scroll from './global/scroll.vue'
    import { mapState, mapMutations, mapGetters } from 'vuex'
    export default {
        name: 'App',
        data() {
            return {
                replenishDialog: false,
                transferDialog: false,
                requestDialog: false,
                historyDialog: false,
                transferMoney: 1,
                transferUser: null,
                requestMoney: 1,
                myTeam: [
                    {
                        value: 1,
                        label: 'Дмитрий Федоров'
                    },
                    {
                        value: 2,
                        label: 'Андрей Царенков'
                    },
                ]
            }
        },
        computed: {
            ...mapState([
                'auth',
                'ddStates',
                'searchTypes',
                'searchTypeCurrent'
            ]),
            ...mapGetters(['fullname', 'aBalance', 'pBalance'])
        },
        components: {
            slideout,
            dropdown,
            scroll
        },
        methods: {
            ...mapMutations(['selectSearchType', 'doLogOut']),
            ddToggle(name) {
                this.ddStates[name] = !this.ddStates[name];
            },
            closeMenu() {
                this.$refs.mainMenu.slideout.close();
            },
            logOut() {
                this.ddStates.profile = false;
                this.doLogOut();
                this.$router.push('/login');
            },
            replenish() {
                this.ddStates.balance = false;
                this.replenishDialog = !this.replenishDialog;
            },
            transfer() {
                this.ddStates.balance = false;
                this.transferDialog = !this.transferDialog;
            },
            request() {
                this.ddStates.balance = false;
                this.requestDialog = !this.requestDialog;
            },
            balanceLog() {
                this.ddStates.balance = false;
                this.historyDialog = !this.historyDialog;
            }
        }
    }
</script>

<style>
    @import '../assets/sass/element-ui.css';
</style>

<style lang="sass">
    @import '../assets/sass/index.sass';
</style>


