import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);
export default new Vuex.Store({
    state: {
        auth: {
            id: 1,
            firstname: 'Евгений',
            lastname: 'Горбатов',
            avatar: '../assets/images/avatar.png',
            since: '12 декабря 2016',
            phone: '+7(964)***-**-05',
            email: 'exs*****@yandex.ru',
            group: 'новый участник',
            aBalance: 10000,
            pBalance: 5000,
            notifications: [],
            regDate: ''
        },
        searchTypeCurrent: 'all',
        searchTypes: {
            all: 'Поиск по сайту',
            catalog: 'Поиск по товарам',
            service: 'Поиск по услугам',
        },
        ddStates: {
            balance: false,
            notifications: false,
            profile: false,
            search: false
        }
    },
    getters: {
        fullname(state) {
            return state.auth.firstname + ' ' + state.auth.lastname;
        },
        pBalance(state) {
            return state.auth.pBalance.toString().replace(/(\d)(?=(\d\d\d)+([^\d]|$))/g, '$1,');
        },
        aBalance(state) {
            return state.auth.aBalance.toString().replace(/(\d)(?=(\d\d\d)+([^\d]|$))/g, '$1,');
        }
    },
    mutations: {
        selectSearchType: (state, name) => {
            state.searchTypeCurrent = name;
            state.ddStates.search = false;
        },
        doLogOut: (state) => {
            state.auth.id = 0;
        },
        SOCKET_CONNECT: (state) => {
            state.connect = true;
        },
        SOCKET_DISCONNECT: (state) => {
            state.connect = false;
        }
    }
})



