import Vue from 'vue';
// import VueSocketIo from 'vue-socket.io';
import {
    Radio,
    Checkbox,
    Input,
    Select,
    Upload,
    Rate,
    Dialog,
    Tooltip,
    Option,
    OptionGroup,
    // DatePicker
} from 'element-ui'
import lang from 'element-ui/lib/locale/lang/ru-RU'
import locale from 'element-ui/lib/locale'
locale.use(lang);

import App from './components/App.vue';
import store from './store/store';
import router from './router/router'
import VueChatScroll from './components/global/down-scroll/vue-scroll'
// Vue.http.headers.common['X-CSRF-TOKEN'] = $('meta[name="token"]').attr('value');

Vue.config.productionTip = false;
// Vue.use(VueSocketIo, 'http://localhost:3000/', store);
Vue.use(Radio, { locale });
Vue.use(Checkbox, { locale });
Vue.use(Input, { locale });
Vue.use(Select, { locale });
Vue.use(Upload, { locale });
Vue.use(Rate, { locale });
Vue.use(Dialog, { locale });
Vue.use(Tooltip, { locale });
Vue.use(Option, { locale });
Vue.use(OptionGroup, { locale });
// Vue.use(DatePicker, { locale });
Vue.use(VueChatScroll);

new Vue({
    el: '#app',
    store,
    router,
    template: '<App />',
    components: { App },
    mounted() {
        document.body.removeAttribute('style');
    },

    sockets: {
        connect: function() {
            console.log('connected');
        }
    }

});

router.beforeEach((to, from, next) => {
    if(!router.app.$store.state.auth.id && to.path != '/login' && to.path != '/register' && to.path != '/recovery' && to.path != '/register-rules')
        next('/login');
    else if (router.app.$store.state.auth.id && (to.path == '/login' || to.path == '/register' || to.path == '/recovery' || to.path == '/register-rules'))
        next(false);
    else
        next();
});


